<template>
  <v-container fluid grid-list-lg>
    <div class="headline">
      <v-icon class="return-icon" large @click="$router.go(-1)">keyboard_return</v-icon>
      Resources
    </div>
    <v-layout row>
      <v-flex>
        <v-card>
          <v-card-text>
            <quill-editor disabled :options="quillOptions" 
              :content="formatValue(resource.guide)">
            </quill-editor>
          </v-card-text>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  name: "Home",
  components: {
    quillEditor
  },
  data: () => ({
    quillOptions: {
      theme: "snow",
      modules: {
        toolbar: false
      },
      readOnly: true,
      placeholder: ""
    }
  }),
  props: ["type"],
  computed: {
    resource() {
      return this.$store.getters.RESOURCES.find(item => item.category === this.type);
    }
  },
  methods: {
    formatValue(val) {
      let tmpVal = decodeURIComponent(val);
      if (!tmpVal || tmpVal === "undefined" || tmpVal === "null" || tmpVal === "<p><br></p>") {
        tmpVal = "";
      }
      return tmpVal;
    }
  }
};
</script>

<style>
.ql-container.ql-snow {
  border: 0;
}
</style>