<template>
  <v-container fluid grid-list-lg>
    <div class="headline">
      <v-icon class="return-icon" large @click="$router.go(-1)">keyboard_return</v-icon>
      {{ decodeURIComponent(diagnosis['name']) }}
      <v-icon v-if="!isFavorite" large @click="toggleFavorite()">star_border</v-icon>
      <v-icon v-if="isFavorite" class="icon-yellow" large @click="toggleFavorite()">star</v-icon>
    </div>
    <v-layout row v-for="section of sections" :key="section">
      <v-flex>
        <v-card>
          <v-card-title class=""><h3>{{ section }}</h3></v-card-title>
          <v-card-text v-if="formatValue(diagnosis[section.toLowerCase().replace(/ /g, '_')]) !== ''">
            <quill-editor disabled :options="quillOptions" 
              :content="formatValue(diagnosis[section.toLowerCase().replace(/ /g, '_')])"
              >
            </quill-editor>
          </v-card-text>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  name: "Home",
  components: {
    quillEditor
  },
  data: () => ({
    sections: ["Conditions", "Clinical Indicators", "Laterality", "Acuity", "Site", "Significance"],
    quillOptions: {
      theme: "snow",
      modules: {
        toolbar: false
      },
      readOnly: true,
      placeholder: ""
    }
  }),
  props: ["id"],
  computed: {
    diagnosis() {
      return this.$store.getters.DIAGNOSIS.find(item => item.id == this.id);
    },
    isFavorite() {
      // see if ID is in the list
      return this.$store.getters.FAVORITES.find(item => item == this.diagnosis.id);
    }
  },
  methods: {
    formatValue(val) {
      let tmpVal = decodeURIComponent(val);
      if (!tmpVal || tmpVal === "undefined" || tmpVal === "null" || tmpVal === "<p><br></p>") {
        tmpVal = "";
      }
      return tmpVal;
    },
    toggleFavorite() {
      if (this.isFavorite) {
        // remove
        this.$store.dispatch("DELETE_FAVORITES", this.diagnosis.id);
      } else {
        // add
        this.$store.dispatch("NEW_FAVORITES", this.diagnosis.id);
      }
    }
  }
};
</script>

<style>
.ql-container.ql-snow {
  border: 0;
}
</style>