<template>
  <v-container fluid grid-list-lg>
    <div class="headline">
      <v-icon class="return-icon" large @click="$router.go(-1)">keyboard_return</v-icon>
      Favorites
    </div>
    <v-layout row v-for="id of favorites" :key="id">
      <v-flex>
        <v-card>
          <v-card-title class="justify-center hover" @click="itemSelection(id)"><h3>{{ decodeURIComponent(diagnosis.find(row => row.id == id).name) }}</h3></v-card-title>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Router from "../router";

export default {
  name: "Home",
  data: () => ({
    menus: []
  }),
  computed: {
    diagnosis() {
      return this.$store.getters.DIAGNOSIS;
    },
    favorites() {
      return this.$store.getters.FAVORITES;
    }
  },
  methods: {
    itemSelection: function(selectedId) {
      Router.push({ name: "diagnosis", params: { id: selectedId } });
    }
  }
};
</script>