<template>
  <v-container fluid grid-list-lg>
    <div class="headline">
      <v-icon class="return-icon" large @click="$router.go(-1)">keyboard_return</v-icon>
      Alerts
    </div>
    <v-layout row v-for="item of alerts" :key="item.id">
      <v-flex>
        <v-card>
          <v-card-text>{{ formatDate(item.created) }}<br>
                      <quill-editor disabled :options="quillOptions" 
              :content="decodeURIComponent(item.alert)"
              >
            </quill-editor></v-card-text>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  name: "Home",
  components: {
    quillEditor
  },
  data: () => ({
    menus: [],
    quillOptions: {
      theme: "snow",
      modules: {
        toolbar: false
      },
      readOnly: true,
      placeholder: ""
    }
  }),
  computed: {
    alerts() {
      return this.$store.getters.RESOURCES.find(item => item.category === "alerts").alerts;
    }
  },
  methods: {
    formatDate(date) {
      const ts = new Date(date);
      return ts.toLocaleDateString("en-US", { month: "long", day: "numeric", year: "numeric" });
    }
  }
};
</script>

<style>
.ql-container.ql-snow {
  border: 0;
}
</style>