<template>
  <v-container fluid grid-list-lg>
    <div v-if="!showPDF">
      <div class="headline">
        <v-icon class="return-icon" large @click="$router.go(-1)">keyboard_return</v-icon>
        Coding Talk
      </div>
      <v-layout row wrap>
        <v-flex xs6 md3 v-for="item of codingTalk" :key="item.title">
          <v-card>
            <v-img class="hover" contain @click="showPdf(item)"
              :src="`./codingtalk/${item.icon}`" 
            ></v-img>
          </v-card> 
        </v-flex>
      </v-layout>
    </div>
    <div v-if="showPDF">
      <div class="headline">
        <v-icon class="return-icon" large @click="showPDF = false">keyboard_return</v-icon>
        {{ pdf.title }}
      </div>
      <v-layout row>
        <v-flex xs12>
        <object class="pdf-viewer" :data="pdf.filename"></object>
        </v-flex>
      </v-layout>
    </div>
  </v-container>
</template>

<script>
import codingTalkLoader from "../assets/codingtalk/codingtalk.json";

export default {
  name: "Home",
  data: () => ({
    codingTalk: codingTalkLoader,
    showPDF: false,
    pdf: { title: null, filename: null }
  }),
  methods: {
    showPdf(item) {
      this.pdf.filename = `./codingtalk/${item.filename}`;
      this.pdf.title = item.title;
      this.showPDF = true;
    }
  }
};
</script>

<style>
.pdf-viewer {
  width: 100%;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  height: calc(100vh - 180px);
}
</style>